.content-footer {
  width: 100%;
  max-width: 880px;
}

@media screen and (min-width: 768px) and (max-width: 1024px) {
  .content-footer {
    max-width: 580px;
  }
}

@media screen and (max-width: 768px) {
  .content-footer {
    display: flex;
    justify-content: center;
    width: 100%;
    max-width: 282px;
  }
}
